<template>
    <div
        :class="`banner-box ${bgClass}`"
        :style="{ backgroundImage: `url(${bg})` }"
    >
        <slot>
            <div class="text">
                <p class="cn-name">{{ cnName }}</p>
                <p class="en-name">{{ enName }}</p>
                <p class="introduction" v-html="Wrap(introduction)"></p>
            </div>
        </slot>

        <!-- 预约演示按钮 -->
        <a class="buttonMore" :href="path">
            <span> 预约演示 </span>
            <img :src="arrowRight" />
        </a>
    </div>
</template>

<script>
import arrowRight from "@/assets/arrow_right.png";
import Common from "@/components/Common";
export default {
    name: "Banner",

    props: {
        enName: {
            type: String,
            default: "",
        },
        cnName: {
            type: String,
            default: "",
        },
        introduction: {
            type: String,
            default: "",
        },
        bg: {
            type: String,
            default: "",
        },
        bgClass: {
            type: String,
            default: "",
        },
    },

    data() {
        return {
            path: "./",
            arrowRight: arrowRight,
        };
    },

    methods: {
        Wrap: (origin) => {
            return Common.wrapFN(origin);
        },
    },
};
</script>

<style lang="scss" scoped>
.banner-box {
    height: 800px;
    background-position: center center;
    background-size: cover;
    color: #ffffff;
    margin-bottom: 20px;

    &.heighter {
        height: 868px;
        .text {
            padding-top: 80px;
        }
        .intro {
            font-size: 30px;
        }
        .intro-stress {
            line-height: 80px;
            font-size: 40px;
        }
        .intro-history {
            font-size: 26px;
            margin: 20px 0 10px;
        }
        .intro-future {
            font-size: 26px;
            line-height: 50px;
        }
    }

    .stress-color {
        color: #ffcc1a;
    }

    .text {
        padding-top: 90px;
        text-align: center;
    }
    .cn-name {
        font-size: 40px;
        color: #ffffff;
        // line-height: 64px;
        // margin-bottom: 28px;
    }
    .en-name {
        font-size: 28px;
        color: #8394b1;
        line-height: 36px;
        margin-bottom: 40px;
    }
    .introduction {
        width: 650px;
        text-align: left;
        margin: 0 auto;
        font-size: 26px;
        color: #ffffff;
        line-height: 1.4;
        margin-bottom: 70px;
    }

    .buttonMore {
        margin: 40px auto;
        display: block;
        width: 180px;
        height: 62px;
        line-height: 62px;
        text-decoration: unset;
        user-select: none;
        font-size: 22px;
        text-align: center;

        box-sizing: border-box;
        color: #1497fb;
        background: #ffffff;
        box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.05);
        border-radius: 6px;

        img {
            width: 20px;
            left: 12px;
        }
    }
}

#scheme{
    .introduction{
        text-align: center;
    }
}
</style>



